<template>
    <Carousel class="s-product-img-wrap" id="gallery" :items-to-show="1" :wrap-around="false" v-model="currentSlide" :class="{'is-alchol' : alchol}">
        <Slide v-for="slide in carousel" :key="slide.id" class="s-product-img">
            <div class="carousel__item s-product-img" :class="{'g-product-video' : slide.imgstyle == 'video'}" @click="slide.productvideoshow = true"><img :src="slide.img" alt=""></div>
        </Slide>
    </Carousel>

    <Carousel id="thumbnails" :items-to-show="4" :wrap-around="true"
        v-model="currentSlide" class="s-product-list-img">
        <Slide  v-for="slide in carousel" :key="slide.id">
            <div class="carousel__item g-carousel-img s-product-img" :class="{'g-product-video' : slide.imgstyle == 'video'}" @click="slideTo(slide.id - 1)">
                <img :src="slide.img" alt="">
            </div>
        </Slide>
        <!-- <template #addons>
            <Navigation />
        </template> -->
    </Carousel>

    <template v-for="s in carousel" :key="s">
        <ModalVideo :modalvideo="s.videolink" v-show="s.productvideoshow" @close="s.productvideoshow = false" v-if="s.imgstyle == 'video'" />
        <template v-else></template>
    </template> 

    


</template>

<script>
    // import CarouselRegular from '../../../global/components/carousel/CarouselRegular.vue'
    import ModalVideo from '../../../global/components/modal/ModalVideo.vue'
    import {
        defineComponent
    } from 'vue'
    import {
        Carousel,
        Slide,
        // Navigation,
    } from 'vue3-carousel'
    export default defineComponent({
        name: 'GalleryPage',
        components: {
            Carousel,
            Slide,
            // Navigation,
            ModalVideo
        },
        props: {
            'alchol': {
                type: Boolean,
                required: false,
            },
        },
        data: () => ({
            currentSlide: 0,
            carousel: [{
                id: 1,
                img: require('../../../global/assets/chicken.png'),
                link: '/productpage',
                imgstyle: 'video',
                videolink: 'https://www.youtube.com/embed/_J2pcVj7eqM',
                productvideoshow: false,
                alchol: true
            }, {
                id: 2,
                img: 'https://i2.momoshop.com.tw/1671702898/goodsimg/0010/910/670/10910670_R1.webp',
                link: '/productpage',
                imgstyle: 'image',
                productvideoshow: false
            }, {
                id: 3,
                img: require('../../../global/assets/chicken.png'),
                link: '/productpage',
                imgstyle: 'image',
                productvideoshow: false
            }, {
                id: 4,
                img: 'https://i2.momoshop.com.tw/1671702898/goodsimg/0010/910/670/10910670_R1.webp',
                link: '/productpage',
                imgstyle: 'image',
                productvideoshow: false
            }, {
                id: 5,
                img: require('../../../global/assets/chicken.png'),
                link: '/productpage',
                imgstyle: 'image',
                productvideoshow: false
            }],

        }),
        methods: {
            slideTo(val) {
                this.currentSlide = val
            },
            productVideo() {
                if (this.carousel.imgstyle == 'video') {
                    return true
                }
            },
            // WrapAround() {
            //     if (this.carousel.index > 4) {
            //         return true
            //     }else{
            //         return false
            //     }
            // },
        },
    })

    
</script>